import { CloudUrl, NO_IMAGE } from "../constants";
import store from "../store";
import { isTagbox, isTagShop, tbox, UPDATE_STATUS } from "./api";
import { isUpdateFrom } from "./commonAction";
import HttpClient from "./http-client";

let imgQue = [];
export let dataSource;
 
export const checkImageUrl = async (url) =>
    ["taggbox.com", "tagbox.com", "tagembed.com", "socialwalls.com"].some((item) => url.includes(item)) || 
    (await fetch(url).then((res) => res.ok).catch(() => false));
 

export const POPUP_IMAGE_RENEW_REQUEST = (event) => {
    event.persist();
    const target = event.target;
    const { network, itemId, themeId } = target.dataset;
    if (network == 1 && ![3, 4, 16, 47, 50, 55, 60].includes(themeId)) {
        const element = document.querySelector(`.tb_media-${itemId}`);
        if (element) element.style.display = "none";
    } else if (!imgQue.includes(event)) {
        imgQue.push(event);
    }
    if (imgQue.length === 1) processImageInQueue();
};

const formatPostData = (data, wall, res) => {
    const { UserPlan, Wall } = wall;
    return {
        table_name: UserPlan?.db_table,
        ...data,
        ...(res?.length ? {
            feedId: res[0]?.feedId,
            type: res[0]?.type,
            link: res[0]?.link,
            postFile: res[0]?.postFile,
            mediaFile: res[0]?.mediaFile,
        } : {}),
        url: Wall?.url,
    };
};


