const processImageInQueue = async () => {
    const { postData, wall } = store.getState().appData;
    if (!wall || !Object.keys(wall).length || !imgQue.length) return;

    const { User, Wall } = wall;
    const ownerId = User?.id;
    const wallId = isTagShop && dataSource === "website" && Wall?.wallId ? Wall.wallId : Wall?.id;

    const target = imgQue[0]?.target;
    const { filterId, network, itemId, load } = target.dataset;
    const imgTargets = document.querySelector(`img[data-item-id="${itemId}"]`);
    const res = postData?.completeDataObject
        ? Object.values(postData.completeDataObject).filter((val) => val.id === itemId)
        : [];

    if (res?.length) {
        const isValid = await checkImageUrl(res[0]?.postFileNew);
        if (!isValid && imgTargets) {
            if (
                [2, 18, 3, 28].includes(+network) &&
                load === "0" &&
                filterId !== "34" &&
                !target.src.includes("cloud.taggbox.com")
            ) {
                const dataFormatted = formatPostData(
                    {
                        wallId,
                        postId: itemId,
                        ownerId,
                    },
                    wall,
                    res
                );
                try {
                    const { data } = await new HttpClient().post(UPDATE_STATUS, dataFormatted, { headers: isUpdateFrom });
                    const baseUrl = isTagShop || isTagbox ? "" : `https://images.${tbox}/`;
                    target.src = network === "18" ? `${baseUrl}${data.media}` : data.media;
                } catch {
                    target.src = NO_IMAGE;
                } finally {
                    dataLoad(target);
                }
            } else {
                target.src = NO_IMAGE;
                dataLoad(target);
            }
            imgQue.shift();
            processImageInQueue();
        } else {
            const src = `${CloudUrl}/media/images/no-image.svg`;
            target.src = src;
            target.srcset = src;
            dataLoad(target);
            imgQue.shift();
        }
    }
};

const dataLoad = (target)=> target.setAttribute("data-load", "1")
 


