import React, { PureComponent } from "react"; 
import Hotspot from "../../../Themes/Elements/Hotspot/Hotspot";
import { CloudUrl } from "../../../../constants";
import { POPUP_IMAGE_RENEW_REQUEST } from "../../../../actions/ImageRenewAction";

export default class PopUpImage extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      mediaLoaded: false
    };
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { Post } = nextProps
    const { media } = Post
    if (this.props.Post !== Post) {
      this.setState({
        imgSize: {
          small: media.image.small,
          large: media.image.large,
          orignal: media.image.orignal
        }
      })
    }
  }
  
  render() {
    const { Post, opacity, mediaSize, Products, Item, ThemeInfo, showTooltip } = this.props
    const { mediaLoaded } = this.state
    const media = Item ? Item : Post.media
    const isHotspot = Products && Products.length > 0 && Post.hotspot ? true : false
    const imgStyle = {
      opacity: opacity, filter: mediaLoaded ? 'blur(2px)' : null,
      aspectRatio: `${mediaSize.width}/${mediaSize.height}`
    }
    return (
      <div className={`tb_post_modal_img_holder`}>
        {isHotspot ? <Hotspot isPopup={true} products={Products} dataItem={Post} showTooltip={showTooltip} /> : null}
        <img
          className="tb_post_modal_img_"
          decoding="async"
          src={media.image.small}
          srcSet={`${media.image.large}, 991w ${media.image.small}, 480w`}
          sizes={mediaSize && mediaSize.width}
          alt={media.title}
          data-link={Post.link}
          data-network={Post.networkId}
          data-wall-id={ThemeInfo.wallId}
          data-owner-id={ThemeInfo.ownerId}
          data-item-id={Post.id}
          data-feed-id={ThemeInfo.feedId ? ThemeInfo.feedId : ""}
          data-load="0"
          onLoad={this.handleImageLoad}
          onError={(e) => {
              e.target.src = `${CloudUrl}/media/error/no-image.svg`
              e.target.srcset = `${CloudUrl}/media/error/no-image.svg`
              POPUP_IMAGE_RENEW_REQUEST(e)
          }}
          loading="lazy"
          draggable="false"
          width={mediaSize && mediaSize.width}
          height={mediaSize && mediaSize.height}
          style={imgStyle} />
      </div>
    );
  }
}

