import React, { memo, useCallback, useMemo, useState, useEffect } from "react";
import { connect } from 'react-redux';
import { Tooltip } from 'react-tooltip';
import "./scss/Hotspot.scss";
import { generateUniqueString } from "../../../../utils/Common";
import { themePostTracking } from "../../../../actions/moredataAction";
import AppendedView from "../../../../AppendedView";

// Memoized components to prevent unnecessary re-renders
const TooltipContent = memo(({ item }) => {
  const hasDiscount = item.discount > 0 && item.discount !== item.price;
  const displayPrice = item.discount > 0 ? item.discount : item.price;

  return (
    <a hotspot={1} href={item.url} target="_blank" rel="noopener noreferrer" className="tb_tooltip_wrap">
      <div hotspot={1} className="tb_p_tooltip_title">{item.title}</div>
      {hasDiscount && (
        <div hotspot={1} className="tb_p_tooltip_price tb_price_disabled">
          {item.currency}{item.price}
        </div>
      )}
      {(item.price > 0 || item.discount > 0) && (
        <div hotspot={1} className="tb_p_tooltip_price">
          {item.currency}{displayPrice}
        </div>
      )}
    </a>
  );
});

const HotspotComponent = ({ products, appData, Post, showTooltip, isPopup, dataItem }) => {
  const [activeIndex, setActiveIndex] = useState(null);

  // Memoized hotspot configuration
  const hotspotConfig = useMemo(() => (
    appData?.wall?.ProductSetting?.Hotspot || {}
  ), [appData]);

  // Effect to handle showTooltip prop
  useEffect(() => {
    if (showTooltip) {
      const productIndex = products.findIndex((product) => product.id === showTooltip);
      if (productIndex !== -1) {
        setActiveIndex(productIndex);
      }
    } else {
      setActiveIndex(null); // Reset active index if showTooltip is 0
    }
  }, [showTooltip, products]);


  // Stable event handlers
  const handleTracking = useCallback((product) => {
    if (appData?.wall) {
      themePostTracking({ type: 1, action: 2, wall: appData?.wall?.ThemeInfo?.webId, product_id: product.id, post: dataItem.referenceId, owner: appData?.wall?.ThemeInfo?.ownerId, feed: dataItem.feedId, currency: product.currency, price: Number(product.price) > 0 ? product.discount : product.price })
    }
  }, [appData, Post]);

  const handleHotspotClick = useCallback((event, item) => {
    event.stopPropagation();
    handleTracking(item);
    window.open(item.url, '_blank');
  }, [handleTracking]);

  if (!hotspotConfig?.status) return null;


  return products.map((item, index) => {
    const tooltipId = `hotspot-${item.id}-${generateUniqueString()}-`;
    const isActive = activeIndex === index;

    return (
      <div
        key={`${item.id}-${index}`}
        id={tooltipId}
        className={`tb_hotspot_dot${isActive ? ' tb_hotspot_active_' : ''}`}
        role="button"
        style={{
          top: item.hotspot.top,
          left: item.hotspot.left
        }}
        onMouseEnter={() => setActiveIndex(index)}
        onMouseLeave={() => setActiveIndex(null)}
        onClick={(e) => { handleHotspotClick(e, item) }}
        data-tooltip-offset={7}
      >
        <div hotspot={1} className={`tb_hotspot__ ${hotspotConfig.type === 0 ? 'tb_hotspot_ani__' : ''}`}>
          {index + 1}
        </div>
        <AppendedView wallId={tooltipId}>
          <Tooltip
            appendTo="body"
            data-tooltip-id={`tooltip-${item.id}-${Math.random().toString(36).substr(2, 9)}`} // Use `data-tooltip-id`
            key={index}
            className="tb_pro_tooltip"
            anchorSelect={`#${tooltipId}`}
            clickable
            {...(isPopup ? { isOpen: isActive } : {})} // Add `isOpen` only when `isPopup` is true
          >
            <TooltipContent item={item} />
          </Tooltip>
        </AppendedView>
      </div>
    );
  });
};


// Memoize the main component
const Hotspot = memo(HotspotComponent);

// Optimized Redux connection
const mapState = (state) => ({ appData: state.appData });
export default connect(mapState)(Hotspot);